import React from 'react';
import {Group, Div, File, FormLayout, CardGrid, Card, Spinner} from "@vkontakte/vkui"
import Icon24Camera from '@vkontakte/icons/dist/24/camera';

class MastersCard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeMasterId: this.props.activeMasterId,
            activeMaster: {},
            photoArr: [],
            isLoad: false
        };
    }
    componentDidMount() {
        fetch('https://jsonplaceholder.typicode.com/photos?albumId=1')
            .then(response => response.json())
            .then(photoArr => {
                this.setState({photoArr: photoArr});
                this.setState({isLoad: true});
            })
        console.log(this.props)
    }
    gridPhoto() {
        return (
            <CardGrid>
                {
                    this.state.photoArr.map(photo => {
                        //console.log(photo);
                        return (
                            <Card size="s" mode="shadow" key={photo.id}>
                                <div style={{height: 96, backgroundImage: 'url('+photo.url+')', backgroundSize: 'cover'}} />
                            </Card>
                        )
                    })
                }
            </ CardGrid>
        )
    }
    render(){
        if(this.state.isLoad===false){
            return (
                <Spinner size="large" style={{ marginTop: 20 }} />
            )
        } else {
            return (
                <Div>
                    <Group title="">
                        <FormLayout>
                            <File top="Добавьте фото в портфолио" before={<Icon24Camera />} size="l">
                                Открыть галерею
                            </File>
                        </FormLayout>
                        <Group separator="hide">
                            {this.gridPhoto()}
                        </Group>
                    </Group>
                </Div>
            )
        }
    }
}

export default MastersCard;